.service-header {
  background: transparent linear-gradient(180deg, #E7ECEC 0%, #E3EAEA 100%) 0% 0% no-repeat padding-box;
}

.service-header__container {
  @include container;

  min-height: 420px;
  padding-top: 106px;
  padding-bottom: 105px;

  background-image: url("../img/midea-desktop.png");
  background-repeat: no-repeat;
  background-position: right 90px top 0;

  @include retina {
    background-image: url("../img/midea-desktop@2x.png");
    background-size: 690px 320px;
  }
}

.service-header__title {
  margin: 0;
  margin-bottom: 15px;

  font-weight: 300;
  font-size: 15px;
  line-height: 16px;

  opacity: 0.8;
}

.service-header__greeting {
  display: block;
  max-width: 50%;
  margin-bottom: 16px;

  font-weight: 300;
  font-size: 37px;
  line-height: 39px;
}

.service-header__description {
  margin: 0;
  margin-bottom: 25px;
  width: 570px;

  font-weight: 300;
  font-size: 15px;
  line-height: 20px;
}

.service-header__link {
  @include link-reset;

  width: 330px;

  font-weight: 300;
  color: #ffffff;
}

.service-header__link span {
  position: relative;

  display: inline-block;
  width: 7px;
  height: 7px;
  margin-left: 16px;

  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: rotate(-45deg);
}

@include desktop-medium {
  .service-header__container {
    min-height: 300px;
    padding-top: 61px;
    padding-bottom: 62px;

    background-image: url("../img/midea-desktop-medium.png");
    background-position: right 53px top 50%;

    @include retina {
      background-image: url("../img/midea-desktop-medium@2x.png");
      background-size: 444px 205px;
    }
  }

  .service-header__title {
    margin-bottom: 25px;
  }

  .service-header__greeting {
    max-width: 49%;
  }

  .service-header__description {
    width: auto;
    max-width: 49%;
  }
}

@include tablet {
  .service-header__container {
    padding-top: 50px;

    background-image: url("../img/midea-tablet.png");
    background-position: right 0 top 50%;

    @include retina {
      background-image: url("../img/midea-tablet@2x.png");
      background-size: 336px 250px;
    }
  }

  .service-header__greeting {
    margin-bottom: 25px;

    line-height: 40px;
  }

  .service-header__description {
    margin-bottom: 45px;
  }

  .service-header__link {
    width: 300px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@include mobile-l {
  .service-header__container {
    position: relative;

    min-height: 200px;
    padding: 42px 32px;

    background-image: url("../img/midea-mobile-large.png");
    background-position: right 5% top 0;

    @include retina {
      background-image: url("../img/midea-mobile-large@2x.png");
      background-size: 192px 210px;
    }
  }

  .service-header__title {
    margin-bottom: 15px;
  }

  .service-header__greeting {
    margin-bottom: 0;

    font-size: 27px;
    line-height: 32px;
  }

  .service-header__description {
    display: none;
  }

  .service-header__link {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    font-size: 0;

    border-radius: 0;
    background-color: transparent;

    &:hover {
      background-color: transparent;
    }
  }

  .service-header__link span {
    display: none;
  }
}

@include mobile {
  .service-header__container {
    padding: 15px 16px;

    background-image: url("../img/midea-mobile.png");
    background-position: right 50% top 125px;

    @include retina {
      background-image: url("../img/midea-mobile@2x.png");
      background-size: 288px 101px;
    }
  }

  .service-header__title {
    margin-bottom: 5px;

    text-align: center;
  }

  .service-header__greeting {
    max-width: 350px;
    margin: 0 auto 150px;

    text-align: center;
  }
}
