.product-registration.js--product-data .product-data {
    display: block;
}

.product-registration.js--contacts .contacts {
  display: flex;
}

.product-registration.js--personal-data .personal-data {
  display: block;
}

.product-registration.js--registration-end .registration-end {
  display: block;
}

.product-registration__container {
  @include container;

  max-width: 1516px;
  padding-left: 53px;
  padding-right: 53px;
  padding-top: 33px;
  padding-bottom: 110px;
}

.breadcrumbs {
  margin-bottom: 80px;

  font-size: 0;
}

.breadcrumbs__link {
  @include link-reset;

  position: relative;

  font-size: 15px;
  line-height: 16px;

  color: #888888;

  &--active {
    color: #00adef;
  }

  &:not(:last-child) {
    margin-right: 28px;
  }

  &:not(:last-child)::before {
    content: "/";

    position: absolute;
    top: 0;
    right: -18px;
  }

  &:hover,
  &:focus {
    color: #00adef;
    outline: none;
  }
}

.product-registration__navigation {
  display: flex;
  width: 100%;

  margin-bottom: 80px;
}

.product-registration__navigation-item {
  position: relative;

  width: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:not(:last-child) {
    margin-right: 30px;
  }

  &:nth-child(2)::before,
  &:last-child::before {
    content: "";

    position: absolute;
    top: 22px;
    right: 50%;

    width: calc(100% - 40px);
    height: 1px;
    margin-right: 35px;

    background-color: #dddddd;
  }

  &.js--active .product-registration__navigation-number {
    background-color: #00adef;
  }

  &.js--active .product-registration__navigation-name {
    color: #000000;
  }

  &.js--active::before {
    background-color: #00adef;
  }

  &.js--checked .product-registration__navigation-number {
    font-size: 0;
  }

  &.js--checked .product-registration__navigation-number::after {
    content: "";

    position: absolute;
    top: 17px;
    left: 14px;

    width: 16px;
    height: 7px;
    border-bottom: 2px solid #ffffff;
    border-left: 2px solid #ffffff;
    transform: rotate(-45deg);
  }
}

.product-registration__navigation--account {
  justify-content: space-between;
}

.product-registration__navigation--account .product-registration__navigation-item {
  width: 32%;

  &:not(:last-child) {
    margin-right: 0;
  }

  &:nth-child(2)::before,
  &:last-child::before {
    content: "";

    position: absolute;
    top: 22px;
    right: 50%;

    width: calc(212.5% - 70px);
    height: 1px;
    margin-right: 35px;

    background-color: #dddddd;
  }

  &.js--active::before {
    background-color: #00adef;
  }
}

.product-registration__navigation-number {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  margin-bottom: 30px;

  font-size: 21px;
  line-height: 23px;
  color: #ffffff;

  background-color: #dddddd;
  border-radius: 50%;
}

.product-registration__navigation-name {
  margin: 0;

  font-size: 19px;
  line-height: 21px;
  color: #888888;
  text-align: center;
}

