@include mobile-l {
  .product-registration__container {
    padding-left: 0;
    padding-right: 0;
  }

  .breadcrumbs {
    padding: 0 32px;
    margin-bottom: 50px;
  }

  .breadcrumbs--account .breadcrumbs__link {
    font-size: 13px;
  }

  .product-registration__navigation {
    width: calc(100% - 64px);
    margin: 0 auto 30px;
  }

  .product-registration__navigation-item {
    &:nth-child(n) {
      width: 33.33%;
      margin-right: 0;
    }

    &:first-child {
      align-items: flex-start;
    }

    &:last-child {
      align-items: flex-end;
    }

    &:nth-child(2)::before {
      width: calc(150% - 84px);
      margin-right: 31px;
    }

    &:last-child::before {
      left: auto;
      right: 53px;

      width: calc(150% - 84px);
      margin-right: 0;
    }
  }

  .product-registration__navigation-name {
    display: none;
  }

  .product-registration__navigation-number {
    margin: 0;
  }

  .product-registration__navigation--account .product-registration__navigation-item {
    &:nth-child(n) {
      width: 50%;
    }

    &:nth-child(2)::before {
      right: 0;

      width: calc(200% - 116px);
      margin-right: 58px;
    }
  }
}

@include mobile {
  .product-registration__container {
    padding-top: 0;
  }

  .product-registration__navigation {
    margin-bottom: 25px;
  }

  .product-registration__navigation-item {
    &:nth-child(2)::before {
      top: 12px;

      width: calc(150% - 48px);
      margin-right: 18px;
    }

    &:last-child::before {
      top: 12px;
      right: 30px;

      width: calc(150% - 48px);
    }

    &.js--checked .product-registration__navigation-number::after {
      top: 9px;
      left: 7px;

      width: 9px;
      height: 4px;
      border-bottom: 1px solid #ffffff;
      border-left: 1px solid #ffffff;
    }
  }

  .product-registration__navigation--account .product-registration__navigation-item {
    &:nth-child(n)::before{
      top: 12px;
    }

    &:nth-child(2)::before {
      width: calc(200% - 60px);
      margin-right: 30px;
    }
  }

  .product-registration__navigation-number {
    width: 24px;
    height: 24px;

    font-size: 17px;
    line-height: 18px;
  }

  .breadcrumbs {
    padding: 17px 16px 16px;
    margin-bottom: 25px;

    background-color: #f6f6f6;
  }

  .breadcrumbs__link {
    font-size: 13px;
    line-height: 15px;

    &:first-child,
    &:last-child {
      display: none;
    }

    &:nth-child(2) {
      padding-left: 25px;
    }

    &:nth-child(2)::before {
      content: "";

      top: 0;
      left: 0;

      width: 10px;
      height: 15px;

      background-image: url("../img/arrow-left.svg");
      background-repeat: no-repeat;
      background-position: top 50% left 0;
    }
  }
}
